@import url(https://fonts.googleapis.com/css?family=Luckiest+Guy&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
    height: 100%;
    background: url(/xoGame/images/bg.png) no-repeat center center fixed;
    /* Center and scale the image nicely */
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    margin: 5px;
}

.container {
    border: 1px solid;
    background-color: white;
}

.logo-image {
    display: block;
    margin: auto;
    width: 25vw;
    margin-top: 2vh;
}

.back-btn {
    display: block;
    margin: auto;
    width: 5vw;
    margin-top: 2vh;
}
.box {
    width: 98vw;
    display: flex;
    margin: auto;
    margin-top: 5vh;
}
td,
th {
    border: 3px solid black;
}

.squaresTable {
    width: 40vw;
    height: 55vh;
    margin: auto;
    background-color: aliceblue;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

@media only screen and (max-width: 800px) {
    .squaresTable {
        height: 45vh;
        width: 45vw;
    }
}

@media only screen and (max-width: 500px) {
    .squaresTable {
        height: 42vh;
        width: 50vw;
    }
}

.wrapper {
    margin: 0 auto;
    display: flex;
}

.full-width {
    width: 100%;
}

.eighty-width {
    width: 80%;
}
.X {
    -webkit-animation: changeToXSign 0.5s forwards;
            animation: changeToXSign 0.5s forwards;
}

.O {
    -webkit-animation: changeToOSign 0.5s forwards;
            animation: changeToOSign 0.5s forwards;
}

@-webkit-keyframes changeToXSign {
    to {background-color: blue;}
}

@keyframes changeToXSign {
    to {background-color: blue;}
}

@-webkit-keyframes changeToOSign {
    to {background-color: red;}
}

@keyframes changeToOSign {
    to {background-color: red;}
}
.select-board-size-dropdown {
    display: table;
    margin: auto;
}
form {
    max-width: 40vw;
    width: 40vw;
    margin: auto;
    margin-top: 5vh;
}

.play-button {
    margin: auto;
    margin-top: 5vh;
    font-style: italic;
    font-size: larger;
    font-weight: 400;
}

.err {
    text-align: center;
    color:red;
    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.back-to-select-mode {
    margin: auto;
    margin-top: 2vh;
}

.form-enter {
    white-space: nowrap;
    -webkit-animation: rightFromLeft 1.5s forwards;
            animation: rightFromLeft 1.5s forwards;
    visibility: visible; 
    
}

@-webkit-keyframes rightFromLeft {
    0% { margin-right:1%; opacity: 0; }
    100%  { margin-right:30%; opacity: 1; }
}

@keyframes rightFromLeft {
    0% { margin-right:1%; opacity: 0; }
    100%  { margin-right:30%; opacity: 1; }
}
.info {
    max-width: 30vw;
    width: 30vw;
    margin: auto;
    margin-top: 5vh;
    text-align: center;
}

@media only screen and (max-width: 700px) {
    .info {
        max-width: 70vw;
        width: 70vw;
    }
}

h1 {
    font-family: monospace;
}

.turn-info {
    font-size: larger;
}
.scroe-board {
    margin: 5px;
    text-align: center;
    margin-top: 20vh;
}

.score-name {
    color: cadetblue;
    font-weight: bold;
}

.score-points {
    font-weight: bold;
}
.mode-btn-div {
  display: inline-flex;
}

.dropdown.show > .dropdown-menu {
    transform: scale(1, 1);  
    opacity:1;
  }
  
  .dropdown-menu {
    opacity:0;
    transform-origin: top;
    -webkit-animation-fill-mode: forwards;  
    animation-fill-mode: forwards; 
    display: block; 
    transition: all 0.3s linear;
    -webkit-transition: all 0.3s linear;
  }

  .moving-left-mode {
    white-space: normal;
    -webkit-animation: leftFromRight 2s forwards;
            animation: leftFromRight 2s forwards;
    visibility: visible; 
}

@-webkit-keyframes leftFromRight {
    0%   { margin-right:35%; }
    /* 99%  { margin-right:70%; opacity: 0 ;visibility: hidden; max-height: 0px; margin-top: 5vh;} */
    100% { margin-right:70%; opacity: 0 ;visibility: hidden; max-height: 0px; margin-top: 0px;}
}

@keyframes leftFromRight {
    0%   { margin-right:35%; }
    /* 99%  { margin-right:70%; opacity: 0 ;visibility: hidden; max-height: 0px; margin-top: 5vh;} */
    100% { margin-right:70%; opacity: 0 ;visibility: hidden; max-height: 0px; margin-top: 0px;}
}

.star-icon {
  width: 1.5vw;
}
.title-h1 {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "Luckiest Guy", cursive;
  margin: 0;
  font-size: 6em;
  padding: 0;
  color: yellow;
  text-shadow: 0 0.1em 20px rgba(0, 0, 0, 1), 0.05em -0.03em 0 rgba(0, 0, 0, 1),
    0.05em 0.005em 0 rgba(0, 0, 0, 1), 0em 0.08em 0 rgba(0, 0, 0, 1),
    0.05em 0.08em 0 rgba(0, 0, 0, 1), 0px -0.03em 0 rgba(0, 0, 0, 1),
    -0.03em -0.03em 0 rgba(0, 0, 0, 1), -0.03em 0.08em 0 rgba(0, 0, 0, 1), -0.03em 0 0 rgba(0, 0, 0, 1);
}

@media only screen and (max-width: 700px) {
  .title-h1 {
    font-size: 5em;
  }
}

@media only screen and (max-width: 600px) {
  .title-h1 {
    font-size: 4em;
  }
}

@media only screen and (max-width: 400px) {
  .title-h1 {
    font-size: 3em;
  }
}

@media only screen and (max-width: 300px) {
  .title-h1 {
    font-size: 2em;
  }
}

.title-span {
    transform: scale(0.9);
    display: inline-block;
  }
  .title-span:first-child {
    -webkit-animation: bop 1s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards infinite
      alternate;
            animation: bop 1s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards infinite
      alternate;
  }
  .title-span:nth-child(2) {
    -webkit-animation: bopA 1s 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards
      infinite alternate;
            animation: bopA 1s 0.3s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards
      infinite alternate;
  }
  .title-span:last-child {
    -webkit-animation: bopB 1s 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards
      infinite alternate;
            animation: bopB 1s 0.6s cubic-bezier(0.175, 0.885, 0.32, 1.275) forwards
      infinite alternate;
  }

  @-webkit-keyframes bopA {
    0% {
      transform: scale(0.9);
    }
    80%,
    100% {
      transform: scale(1) rotateZ(3deg);
    }
  }

  @keyframes bopA {
    0% {
      transform: scale(0.9);
    }
    80%,
    100% {
      transform: scale(1) rotateZ(3deg);
    }
  }

@-webkit-keyframes bop {
  0% {
    transform: scale(0.9);
  }
  50%,
  100% {
    transform: scale(1);
  }
}

@keyframes bop {
  0% {
    transform: scale(0.9);
  }
  50%,
  100% {
    transform: scale(1);
  }
}

@-webkit-keyframes bopB {
  0% {
    transform: scale(0.9);
  }
  80%,
  100% {
    transform: scale(1) rotateZ(-3deg);
  }
}

@keyframes bopB {
  0% {
    transform: scale(0.9);
  }
  80%,
  100% {
    transform: scale(1) rotateZ(-3deg);
  }
}

